<script>
import achieves from "@/assets/data/achieve.json";
import { __imgPath } from "@jx3box/jx3box-common/data/jx3box.json";
import {
    getVideos,
    deleteVideo,
    addVideo,
    updateVideo,
} from "@/service/race.js";
import { default_avatar } from "@jx3box/jx3box-common/data/jx3box.json";
import { getThumbnail,getLink } from "@jx3box/jx3box-common/js/utils";
import User from "@jx3box/jx3box-common/js/user";
import servers from "@jx3box/jx3box-data/data/server/server_list.json";
import _ from "lodash";
export default {
    props: [],
    data: function() {
        return {
            video_title_img: __imgPath + "image/rank/common/videos.png",
            data: [],
            per: 24,
            page: 1,
            total: 1,
            loading: false,
            servers,
            server: "",
            current_boss: "",
            isSuperAdmin: User.isSuperAdmin(),
            dialogVisible: false,
            video: {
                ID: "",
                team_id: "",
                event_id: "",
                aid: "",
                title: "",
                url: "",
            },
        };
    },
    computed: {
        id: function() {
            return this.$store.state.id;
        },
        params: function() {
            return {
                pageSize: this.per,
                pageIndex: this.page,
                aid : this.current_boss
            };
        },
        bossList: function() {
            return achieves[this.id] || [];
        },
        demo_video: function() {
            return {
                team_id: "",
                event_id: ~~this.id,
                aid: "",
                title: "",
                url: "",
            };
        },
    },
    methods: {
        changeBoss: function(val) {
            this.current_boss = val;
            this.page = 1;
            this.loadData()
        },
        loadData: function() {
            this.loading = true;
            getVideos(this.id, this.params)
                .then((res) => {
                    this.data = res.data.data.list;
                    this.page = res.data.data.page.index;
                    this.total = res.data.data.page.total;
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        del: function(id) {
            this.$alert("确认删除吗", "消息", {
                confirmButtonText: "确定",
                callback: (action) => {
                    if (action == "confirm") {
                        deleteVideo(id).then((res) => {
                            this.$message({
                                type: "success",
                                message: `删除成功`,
                            });

                            location.reload();
                        });
                    }
                },
            });
        },
        edit: function(item) {
            this.dialogVisible = true;
            this.video = item;
        },
        add: function() {
            this.dialogVisible = true;
            this.video = _.cloneDeep(this.demo_video);
        },
        submit: function() {
            if (this.video.ID) {
                updateVideo(this.video.ID,this.video).then((res) => {
                    this.$message({
                        type: "success",
                        message: `更新成功`,
                    });
                    this.dialogVisible = false;
                });
            } else {
                addVideo(this.video).then((res) => {
                    this.$message({
                        type: "success",
                        message: `发布成功`,
                    });
                    this.dialogVisible = false;
                });
            }
        },
    },
    watch: {
        params: {
            deep: true,
            handler: function() {
                this.loadData();
            },
        },
    },
    filters: {
        teamLogo: function(val) {
            return val ? getThumbnail(val, 120, true) : default_avatar;
        },
        liveAvatar: function(val) {
            return val ? getThumbnail(val, 68, true) : default_avatar;
        },
        teamLink: function(val) {
            // return "/team/#/org/view/" + val;
            return getLink('org',val);
        },
        videoCover: function(aid) {
            return __imgPath + `image/rank/videos/${aid}.png`;
        },
    },
    created: function() {
        this.loadData();
    },
    components: {},
};
</script>

